    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            houseSelect
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            }
        }
    })
], Home);
export default Home;
